/* Cookie Bar */
.cookie {
  position: fixed;
  left: 0;
  bottom: 0;
  background: #fff;
  padding: 26px;
  z-index: 99;
  width: 100%;
  max-width: 360px;
  color: #000;
  margin: 20px;
  box-shadow: 0 17px 30px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  transition: all 0.3s cubic-bezier(0.52, 0.16, 0.24, 1);
  transform: translateY(200px);
}

.cookie.--active {
  transform: translateY(0);
}

@media (max-width: 450px) {
  .cookie {
    margin: 20px auto;
    max-width: 100%;
    width: 90%;
    right: 0;
  }
}

.cookie p {
  font-size: 13px;
  line-height: 24px;
}

.cookie p a {
  box-shadow: 0 1px 0 0 #bfbfbf;
}

.cookie .button {
  width: 100%;
  text-align: center;
  font-size: 10.3px !important;
}

.cookie__close {
  position: absolute;
  right: 12px;
  top: 12px;
  padding: 10px;
  line-height: 10px;
  font-size: 11px;
  background: transparent;
  outline: none;
  border: none;
  float: right;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.52, 0.16, 0.24, 1);
}

.cookie__close:hover {
  opacity: .6;
  background: transparent;
}

/* Accessibility */
/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #231f20;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/* Alignments */
.alignleft {
  /*rtl:ignore*/
  float: left;
  /*rtl:ignore*/
  margin-right: 25px;
  margin-bottom: 25px;
}

@media (min-width: 992px) {
  .alignleft {
    /*rtl:ignore*/
    margin-right: calc(2 * 1rem);
  }
}

.alignright {
  /*rtl:ignore*/
  float: right;
  /*rtl:ignore*/
  margin-left: 25px;
  margin-bottom: 25px;
}

@media (min-width: 992px) {
  .alignright {
    /*rtl:ignore*/
    margin-left: calc(2 * 1rem);
  }
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* Clearings */
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.footer:before,
.footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.footer:after {
  clear: both;
}

