/* Content */
/* Header */
/* Header */
.header {
  position: relative;
  background-color: rgba(255, 255, 255, 0.97);
  box-shadow: 0 8px 18px rgba(208, 210, 214, 0.2);
  z-index: 9990;
  transform: translateZ(0);
  transform: translate3d(0, 0, 0);
}

@media (max-width: 992px) {
  .header {
    width: 100%;
    padding: 0;
    z-index: 9999;
    background-color: #fff;
  }
}

.header__no-menu {
  font-size: 14px;
  color: #000;
  z-index: 99;
}

.header__no-menu:hover {
  color: #bbbdbd;
}

@media (max-width: 992px) {
  .header__no-menu {
    color: #fff;
    display: block;
    padding: 40px 30px;
    opacity: 0;
    -webkit-transform: translateY(-44px);
    transform: translateY(-44px);
    -webkit-transition: all 0.56s cubic-bezier(0.52, 0.16, 0.24, 1);
    transition: all 0.56s cubic-bezier(0.52, 0.16, 0.24, 1);
    transition-delay: .1s;
  }
  .header__no-menu:hover {
    color: #bbbdbd;
  }
}

/* Header Ad Bar */
.header__placement {
  overflow: hidden;
  background-color: #efefef;
  text-align: center;
  transition: -webkit-transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.header__placement p {
  margin: 0;
}

.header__placement img {
  vertical-align: top;
  transition: all 0.3s cubic-bezier(0.52, 0.16, 0.24, 1);
}

.header__placement a:hover img {
  opacity: .8;
}

@media (max-width: 992px) {
  .header__placement {
    display: none;
  }
}

/* Header Top Wrapper */
.header__top {
  position: relative;
  padding: 0 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 8;
  height: 64px;
}

/* Header Logo */
.header__logo {
  max-width: 200px;
}

.header__logo h1 {
  font-size: 20px;
  letter-spacing: 0;
  transition: all 0.3s cubic-bezier(0.52, 0.16, 0.24, 1);
}

.header__logo p {
  margin: 0;
}

.header__logo img {
  vertical-align: top;
  max-width: 100%;
}

.header__logo:hover {
  color: #000;
}

.header__logo:hover h1, .header__logo:hover h2 {
  color: #000 !important;
}

@media (max-width: 992px) {
  .header__logo {
    position: relative;
    display: block;
    width: 100%;
    text-align: center;
    z-index: -1;
    max-width: 260px;
    margin: 0 auto;
    position: absolute;
    left: 0;
    right: 0;
  }
}

/* Header Social */
.social {
  position: relative;
  display: inline-block;
  height: 25px;
  border-right: 1px solid rgba(119, 119, 119, 0.1);
  padding-right: 8px;
  margin-right: 8px;
}

.social__link {
  font-size: 12px;
  color: #000;
  padding: 8px;
}

@media (max-width: 992px) {
  .social__link {
    color: #fff;
  }
}

@media (max-width: 992px) {
  .header__top .social {
    display: none;
  }
}

.header__nav .social {
  position: relative;
  display: block;
  width: 100%;
  text-align: left;
  margin: 20px 0 40px 0;
  padding: 0 25px;
  border: none;
  opacity: 0;
  -webkit-transform: translateY(-34px);
  transform: translateY(-34px);
  -webkit-transition: transform .26s, opacity .26s;
  transition: transform .26s, opacity .26s;
}

.header--open .header__nav .social {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  -webkit-transition: transform 0.66s cubic-bezier(0.52, 0.16, 0.24, 1), opacity 0.66s cubic-bezier(0.52, 0.16, 0.24, 1);
  transition: transform 0.66s cubic-bezier(0.52, 0.16, 0.24, 1), opacity 0.66s cubic-bezier(0.52, 0.16, 0.24, 1);
  transition-delay: .36s;
}

@media (min-width: 992px) {
  .header__nav .social {
    display: none;
  }
}

.header--style-2 .social,
.header--style-4 .social {
  border-right: none;
  padding-right: 0;
  margin-right: 0;
}

/* Header Icons */
.header svg {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: .3;
}

.header__search svg {
  width: 15px;
  height: 15px;
  stroke-width: .4;
}

.header__cart svg {
  width: 15px;
  height: 15px;
  stroke-width: .2;
}

.header__icons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__search {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 0 8px 8px;
}

.header__cart {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: 10px 11px;
  text-align: center;
  padding: 8px;
  border-radius: 50%;
  background-clip: padding-box;
}

.header__cart span {
  position: absolute;
  top: 3px;
  right: -2px;
  width: 11px;
  height: 11px;
  line-height: 10px;
  font-size: 8px;
  font-weight: 400;
  text-align: center;
  background: #f5f5f5;
  border: 1px solid #f1e8dd;
  border-radius: 50px;
  color: #000;
}

/* Header Hamburger */
.header__ham {
  height: 40px;
  width: 30px;
  cursor: pointer;
  z-index: 9;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
}

.header__ham:hover {
  opacity: 0.7;
}

@media (min-width: 992px) {
  .header__ham {
    display: none;
  }
}

.header__ham-inner, .header__ham-inner::before, .header__ham-inner::after {
  width: 18px;
  height: 2px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.header__ham-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
  transition-duration: 0.22s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.header__ham-inner::before, .header__ham-inner::after {
  content: '';
  display: block;
}

.header__ham-inner::before {
  top: -5px;
  transition: top 0.2s 0.35s ease-in, opacity 0.2s ease-in;
}

.header__ham-inner::after {
  bottom: -5px;
  transition: bottom 0.2s 0.35s ease-in, transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.header--open .header__ham:hover {
  opacity: 0.7;
}

.header--open .header__ham-inner {
  transform: rotate(225deg);
  transition-delay: 0.22s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

